.navbar {
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 0.8rem;
  font-weight: bold;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
  font-family: "Poppins", sans-serif;
  background: white;
}

.navbar-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;

  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  -webkit-justify-content: end;
          justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: hsl(0, 0%, 75%);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: hsl(0, 0%, 75%);
}

.nav-links-mobile {
  display: none;
}

.hamburger-menu {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-item {
    height: 3rem;
  }

  .nav-menu.active {
    background: hsl(257, 27%, 26%);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    grid-gap: 0;
    gap: 0;
    font-size: 1rem;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(22%, 67%);
            transform: translate(22%, 67%);
  }

  .hamburger-menu {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 45%);
            transform: translate(-100%, 45%);

    cursor: pointer;
    font-size: 1.8rem;
  }

  .fa-times {
    color: hsl(0, 0%, 75%);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 1rem;
    width: 80%;
    text-decoration: none;
    font-size: 1rem;
    background-color: var(--cyan);
    color: #fff;
    padding: 14px 20px;

    transition: all 0.3s ease-out;
  }
  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}

